.profilePage {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: center;
  padding: 20px;
}

.profileBanner {
  display: flex;
  border-radius: 6px;
  background-color: var(--primary-color-light);
  transition: var(--trans-05);
  justify-content: center;
  align-items: center;
  height: 75px;
  width: 100%;
}
.profileBanner .profilePicture {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--body-color);
  transition: var(--trans-05);
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 2px solid var(--sidebar-color);
  top: 35px;
}
.profileBanner .profilePicture img {
  position: absolute;
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.profileContainer {
  position: relative;
  display: flex;
  align-items: baseline;
  justify-content: center;
  width: 100%;
  padding: 70px 10px 10px 10px;
  flex-wrap: wrap;
  gap: 10px;
}
.profileContainer p {
  color: var(--text-color);
  transition: var(--trans-02);
}
.profileContainer .profileData,
.profileContainer .profileStats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: auto;
  padding: 16px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
  background-color: var(--primary-color-light);
  color: var(--text-color);
  transition: var(--trans-05);
}
.profileContainer .profileData {
  align-items: start;
}
.profileContainer .profileData input {
  font-size: 16px;
  background-color: var(--primary-color-light);
  color: var(--text-color);
  transition: var(--trans-02);
  width: 150px;
}
.profileContainer .profileData input:disabled {
  outline: none;
  border: none;
}
.profileContainer .profileStats {
  align-items: start;
  gap: 20px;
}
.profileContainer .profileStats table {
  border-collapse: collapse;
}
.profileContainer .profileStats table tbody td {
  padding: 1px 5px 1px 0;
}
.profileContainer .deleteAccount {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  border: 1px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}
.profileContainer .deleteAccount p {
  margin-bottom: 15px;
}
.profileContainer .deleteAccount button {
  padding: 10px 15px;
  margin-right: 10px;
  cursor: pointer;
}
.profileContainer .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.profileContainer .overlay.active {
  display: flex;
}

