.homeContainer,
.roomsContainer {
  display: flex;
  align-items: baseline;
  justify-content: center;
  padding: 20px 20px;
  flex-wrap: wrap;
  gap: 10px;
}

.homeContainer .soloContainer,
.homeContainer .multiContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 16px;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.24) 0 3px 8px;
  background-color: var(--primary-color-light);
  color: var(--text-color);
  transition: var(--trans-05);
}
.homeContainer .soloContainer h1, .homeContainer .soloContainer form,
.homeContainer .multiContainer h1,
.homeContainer .multiContainer form {
  color: var(--text-color);
  transition: var(--trans-05);
}
.homeContainer .soloContainer form,
.homeContainer .multiContainer form {
  display: flex;
  flex-direction: column;
}
.homeContainer .soloContainer form input,
.homeContainer .multiContainer form input {
  outline: none;
  border-radius: 6px;
  padding: 1px 2px 1px 5px;
  background-color: var(--primary-color-light);
  color: var(--text-color);
  transition: var(--trans-05);
}
.homeContainer .soloContainer form button,
.homeContainer .multiContainer form button {
  margin: 10px;
}
.homeContainer .soloContainer .formElement,
.homeContainer .multiContainer .formElement {
  display: flex;
  flex-direction: column;
}
.homeContainer .soloContainer .formElement .error,
.homeContainer .multiContainer .formElement .error {
  color: red;
  font-size: 12px;
}
.homeContainer .soloContainer .formElementCheckBox,
.homeContainer .multiContainer .formElementCheckBox {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.homeContainer .soloContainer .formElementCheckBox label,
.homeContainer .multiContainer .formElementCheckBox label {
  margin-right: 5px;
}

.roomsContainer .roomsList {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: 50%;
  padding: 16px;
}
.roomsContainer .roomsList .error {
  color: red;
  font-size: 16px;
}
.roomsContainer .roomsList .syncRooms {
  font-size: 20px;
}
.roomsContainer .roomsList table {
  color: var(--text-color);
  border-collapse: collapse;
}
.roomsContainer .roomsList table th, .roomsContainer .roomsList table td {
  border: 1px solid #ddd;
}
.roomsContainer .roomsList table td {
  padding: 15px;
}
.roomsContainer .roomsList table thead {
  color: var(--menu--color);
  background-color: var(--primary-color);
  transition: var(--trans-05);
}
.roomsContainer .roomsList table thead th, .roomsContainer .roomsList table thead td {
  padding: 15px;
  text-align: left;
}
.roomsContainer .roomsList table tbody {
  background: var(--sidebar-color);
  transition: var(--trans-05);
}
@media screen and (max-width: 600px) {
  .roomsContainer .roomsList table table {
    border: 0;
  }
  .roomsContainer .roomsList table table tr {
    margin-bottom: 20px;
    display: flex;
  }
  .roomsContainer .roomsList table table td {
    display: block;
    text-align: right;
    width: 100%;
    padding: 10px;
  }
}

@media (max-width: 600px) {
  .homeContainer {
    flex-direction: column;
  }
}

