.loginForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loginForm h1 {
  text-align: center;
  margin-bottom: 30px;
  color: var(--text-color);
}
.loginForm form {
  max-width: 300px;
  margin: 0 auto;
}
.loginForm form input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 15px;
}
.loginForm form input[type=submit] {
  background-color: var(--primary-color);
  color: white;
  border: none;
  cursor: pointer;
}
@media (max-width: 600px) {
  .loginForm form {
    max-width: 100%;
  }
  .loginForm input {
    padding: 8px;
  }
}

