.canvasPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.canvasPage .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: fit-content;
}
.canvasPage .container .canvasInfos {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.canvasPage .container .canvasInfos .bombsContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.canvasPage .container .canvasInfos .timer, .canvasPage .container .canvasInfos .bombs {
  font-weight: 500;
}
.canvasPage .container .canvasInfos .bombs {
  margin-left: 1rem;
}
.canvasPage .container .canvasInfos .timer {
  margin-right: 1rem;
}
.canvasPage .container .canvasContainer {
  display: flex;
}

.multiContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.multiContainer .startContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: #fff;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  margin-bottom: 0.5rem;
}
.multiContainer .gameContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.multiContainer .gameContainer .playersContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.multiContainer .gameContainer .playersContainer .playerInfo {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: #fff;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  margin-bottom: 0.5rem;
}
.multiContainer .gameContainer .playersContainer .playerInfo span {
  font-weight: 500;
}
.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 35%;
  left: 45%;
}

.modalContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: fit-content;
  padding: 1rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
}
.modalContent .modalHeader, .modalContent .modalBody, .modalContent .modalFooter {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: fit-content;
  padding: 1rem;
}
.modalContent .modalHeader .close {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 2rem;
  height: 2rem;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

